*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  position: relative;
  background: url("../images/bg-intro-mobile.png"), #ff7a7a;
  background-position: center top;
  user-select: none;
  min-height: 100vh;
  text-align: center;
}
@media (min-width: 768px) {
  body {
    background: url("../images/bg-intro-desktop.png"), #ff7a7a;
    background-position: center top;
  }
}

@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-Regular.ttf") format("truetype");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-Medium.ttf") format("truetype");
  font-style: normal;
  font-weight: 500;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-SemiBold.ttf") format("truetype");
  font-style: normal;
  font-weight: 600;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-Bold.ttf") format("truetype");
  font-style: normal;
  font-weight: 700;
}
body {
  font-family: "Poppins", sans-serif;
  font-size: 16px;
}

.heading-primary {
  font-size: clamp(29px, 3.9vw, 49px);
  color: #fff;
  line-height: 1.25;
}
@media (min-width: 768px) {
  .heading-primary {
    line-height: 1.12;
  }
}

.paragraph {
  color: #fff;
  margin-top: 22px;
  line-height: 1.63;
}
@media (min-width: 768px) {
  .paragraph {
    margin-top: 28px;
  }
}

input, button {
  font-family: "Poppins", sans-serif;
}

.container {
  max-width: 1110px;
  width: 87%;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .row {
    display: flex;
    justify-content: space-between;
  }
}

.btn-submit {
  width: 100%;
  font-size: 15.5px;
  text-transform: uppercase;
  font-weight: 500;
  letter-spacing: 0.8px;
  color: #fff;
  background-color: #38cc8c;
  border: none;
  border-bottom: 4px solid #30bb7f;
  border-radius: 5px;
  padding: 18px 15px 11px 15px;
  margin-top: 16px;
  cursor: pointer;
  transition: transform 0.2s ease-in;
}
.btn-submit:hover {
  transform: scale(1.05);
  background-color: #75dbaf;
  border-bottom-color: #4dd198;
}
.btn-submit:active, .btn-submit:focus {
  transform: scale(1);
}
@media (min-width: 768px) {
  .btn-submit {
    margin-top: 4.3%;
  }
}

.form {
  background-color: #fff;
  padding: 24px;
  border-radius: 10px;
  box-shadow: 0 8px 0 rgba(0, 0, 0, 0.15);
}
@media (min-width: 768px) {
  .form {
    padding: 7.4%;
  }
}
.form__group {
  position: relative;
}
.form__group:not(:first-child) {
  margin-top: 16px;
}
@media (min-width: 768px) {
  .form__group:not(:first-child) {
    margin-top: 4.4%;
  }
}
.form__error-img {
  display: none;
  position: absolute;
  top: 50%;
  right: 5.8%;
  transform: translateY(-50%);
}
.form__error-img.active {
  display: block;
}
.form__validation-msg {
  display: none;
  font-size: 11px;
  font-weight: 500;
  text-align: end;
  font-style: italic;
  color: #ff7a7a;
  margin-top: 4px;
}
.form__validation-msg.active {
  display: block;
}
.form__input {
  display: block;
  font-size: 14px;
  font-weight: 600;
  color: #3e3c49;
  width: 100%;
  padding: 16px 51px 16px 18px;
  border: 1px solid rgba(185, 182, 211, 0.5);
  border-radius: 5px;
}
.form__input:focus {
  outline: 1px solid #3e3c49;
}
.form__input.invalid {
  outline: 2px solid #ff7a7a;
}
.form__input::placeholder {
  font-family: inherit;
  font-size: 14.2px;
  font-weight: 600;
  color: black;
}
@media (min-width: 768px) {
  .form__input {
    padding: 16px 61px 16px 31px;
  }
}
.form__legal {
  font-size: 11px;
  line-height: 1.9;
  text-align: center;
  color: #b9b6d3;
  margin-top: 9px;
  padding: 0 20px;
}
.form__legal--highlight {
  text-decoration: none;
  color: #ff7a7a;
  font-weight: 700;
}
@media (min-width: 768px) {
  .form__legal {
    margin-top: 11px;
  }
}

.intro {
  padding: 24.2% 0 18.1% 0;
  max-width: 540px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .intro {
    padding: 8.5% 0;
    max-width: 100%;
    width: 100%;
    margin: 0;
  }
}
.intro__text {
  text-align: center;
  margin-bottom: 62px;
}
@media (min-width: 768px) {
  .intro__text {
    text-align: start;
    margin: 0;
    flex-basis: 45%;
    align-self: center;
    transform: translateY(4px);
  }
}
@media (min-width: 768px) {
  .intro__signup {
    flex-basis: 48.65%;
  }
}
.intro__offer {
  font-size: 15.4px;
  text-align: center;
  font-weight: 400;
  line-height: 1.7;
  color: #fff;
  background-color: #6055a5;
  padding: 20px 50px 16px 50px;
  border-radius: 10px;
  margin-bottom: 24px;
  box-shadow: 0 8px 0 rgba(0, 0, 0, 0.15);
}
.intro__offer--highlight {
  font-weight: 700;
}
@media (min-width: 768px) {
  .intro__offer {
    padding: 17px 50px 16px 50px;
  }
}

.footer {
  position: relative;
  display: inline-block;
  padding: 10px 15px;
  margin: 0 10%;
  color: #fff;
  border-radius: 10px;
  overflow: hidden;
}
.footer__attribution {
  font-size: 16px;
  text-align: center;
}
.footer__link {
  font-size: 22px;
  font-weight: 700;
  text-decoration: none;
  color: #fff;
  animation: text-glow 3s ease infinite;
}

@keyframes text-glow {
  from {
    text-shadow: 0 0 1px #fff, 0 0 2px #fff;
  }
  to {
    text-shadow: none;
  }
}
.flag {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.flag__color {
  display: block;
  filter: contrast(50%);
  width: 100%;
  height: 33.3%;
  left: 0;
}
.flag__color--1 {
  background-color: rgba(255, 0, 0, 0.5);
  animation: animate-flag 3s ease infinite;
}
.flag__color--2 {
  background-color: rgba(0, 0, 255, 0.5);
  animation: animate-flag 3s ease infinite 0.33s;
}
.flag__color--3 {
  background-color: rgba(255, 255, 255, 0.5);
  animation: animate-flag 3s ease infinite 0.66s;
}

@keyframes animate-flag {
  0% {
    transform: scale(0);
  }
  25% {
    transform: scale(1);
  }
  50% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}